import React from 'react';
import teks from '../data';

const FeaturesSection = () => {
  const getFeatureIcon = (index) => {
    const icons = ['💬', '⚡', '📊'];
    return icons[index] || '✨';
  };

  return (
    <>
      {/* Seção 1: Automação com empatia */}
      <section className="teknolab-section" id="recursos">
        <div className="teknolab-container">
          <header className="teknolab-section__header">
            <h2>{teks.section1.title}</h2>
          </header>
          
          <div className="teknolab-features">
            {teks.section1.features.map((feature, index) => (
              <article key={index} className="teknolab-feature">
                <div className="teknolab-feature__icon" aria-hidden="true">
                  {getFeatureIcon(index)}
                </div>
                <h3>{feature.title}</h3>
                <p>{feature.desc}</p>
              </article>
            ))}
          </div>
        </div>
      </section>

      {/* Seção 2: Controle em tempo real */}
      <section className="teknolab-section">
        <div className="teknolab-container">
          <header className="teknolab-section__header">
            <div className="teknolab-section__label">{teks.section2.label}</div>
            <h2>{teks.section2.title}</h2>
          </header>
          
          <div className="teknolab-features">
            {teks.section2.miniFeatures.map((feature, index) => (
              <article key={index} className="teknolab-feature">
                <div className="teknolab-feature__icon" aria-hidden="true">
                  {['🔍', '🎯', '👥'][index]}
                </div>
                <h3>{feature.title}</h3>
                <p>{feature.desc}</p>
              </article>
            ))}
          </div>
        </div>
      </section>

      {/* Seção 3: Transforme fluxos em receita */}
      <section className="teknolab-section">
        <div className="teknolab-container">
          <header className="teknolab-section__header">
            <div className="teknolab-section__label">{teks.section3.label}</div>
            <h2>{teks.section3.title}</h2>
          </header>
          
          <div className="teknolab-features">
            {teks.section3.features.map((feature, index) => (
              <article key={index} className="teknolab-feature">
                <div className="teknolab-feature__icon" aria-hidden="true">
                  {['📋', '📈', '🤝'][index]}
                </div>
                <h3>{feature.title}</h3>
                <p>{feature.desc}</p>
              </article>
            ))}
          </div>
        </div>
      </section>
    </>
  );
};

export default FeaturesSection; 